/* ============= GLOBAL SECTION SPACING ============= */
section {
  margin-bottom: 90px;
}

/* ============= HERO ============= */
.clean-hero {
  position: relative;
  background: linear-gradient(180deg, #ffffff, #f4f7ff);
  padding: 110px 20px 90px;
  overflow: hidden;
}

/* Animated background blobs */
.clean-hero::before,
.clean-hero::after {
  content: "";
  position: absolute;
  width: 420px;
  height: 420px;
  background: radial-gradient(circle, rgba(249,115,22,0.18), transparent 70%);
  border-radius: 50%;
  animation: float 12s infinite ease-in-out;
  z-index: 0;
}

.clean-hero::before {
  top: -120px;
  left: -120px;
}

.clean-hero::after {
  bottom: -140px;
  right: -140px;
  animation-delay: 6s;
}

@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(25px); }
  100% { transform: translateY(0px); }
}

.clean-hero-inner {
  max-width: 1200px;
  margin: auto;
  position: relative;
  z-index: 2;
}

/* Badge */
.hero-badge {
  display: inline-block;
  background: #ecfdf3;
  color: #166534;
  padding: 7px 16px;
  border-radius: 999px;
  font-size: 13px;
  margin-bottom: 18px;
}

/* Headings */
.clean-hero h1 {
  font-size: 44px;
  font-weight: 800;
  color: #111827;
  line-height: 1.15;
}

.clean-hero h1 span {
  color: #f97316;
}

.clean-hero p {
  margin-top: 18px;
  color: #4b5563;
  max-width: 620px;
  line-height: 1.8;
}

/* Buttons */
.hero-buttons {
  margin-top: 36px;
  display: flex;
  gap: 18px;
}

.primary-btn {
  background: #f97316;
  color: white;
  border-radius: 999px;
  padding: 14px 34px;
  border: none;
  font-weight: 600;
  transition: 0.3s;
}

.primary-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 28px rgba(249,115,22,0.3);
}

.outline-btn {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 999px;
  padding: 14px 34px;
  font-weight: 600;
  transition: 0.2s;
}

.outline-btn:hover {
  background: #f9fafb;
}

.hero-benefits {
  margin-top: 26px;
  display: flex;
  gap: 30px;
  font-size: 14px;
  color: #374151;
}

/* ============= METRICS ============= */
.metrics-section {
  display: flex;
  justify-content: center;
  gap: 120px;
  padding: 70px 20px;
  background: white;
}

.metric-card h2 {
  font-size: 38px;
}

/* ============= WHY SECTION ============= */
/* WRAPPER */
.why-clean {
  max-width: 1200px;
  margin: auto;
  padding: 80px 20px;
  display: grid;
  grid-template-columns: 1.1fr 1fr;
  gap: 60px;
  align-items: center;
}

/* LEFT CONTENT */
.why-left h2 {
  font-size: 38px;
  font-weight: 800;
  color: #111827;
  line-height: 1.3;
  margin-bottom: 14px;
}

.why-left p {
  font-size: 17px;
  line-height: 1.7;
  color: #4b5563;
  margin-bottom: 20px;
  max-width: 540px;
}

/* BULLET LIST */
.why-left ul {
  margin-top: 20px;
  padding-left: 0;
  list-style: none;
}

.why-left ul li {
  font-size: 16px;
  margin-bottom: 14px;
  display: flex;
  align-items: center;
  gap: 12px;
  color: #1f2937;
  font-weight: 500;
}

.why-left ul li::before {
  content: "âœ”";
  background: #f97316;
  color: white;
  font-size: 13px;
  padding: 2px 6px;
  border-radius: 6px;
}

/* RIGHT GRID BOXES */
.why-right {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 18px;
}

.why-box {
  background: white;
  padding: 26px;
  border-radius: 14px;
  box-shadow: 0 8px 20px rgba(0,0,0,0.08);
  font-weight: 600;
  text-align: center;
  color: #111827;
  transition: 0.25s ease;
}

.why-box:hover {
  transform: translateY(-6px) scale(1.02);
  box-shadow: 0 14px 32px rgba(0,0,0,0.12);
}


/* ==========================================
   BEAUTIFUL OUR SERVICES SECTION
   ========================================== */
.services-clean {
  background: #f9fafb;
  padding: 100px 20px;
  text-align: center;
}

/* Heading */
.services-heading {
  font-size: 40px;
  font-weight: 800;
  color: #111827;
  position: relative;
  margin-bottom: 12px;
}

/* Gradient underline */
.services-heading::after {
  content: "";
  width: 90px;
  height: 5px;
  border-radius: 6px;
  background: linear-gradient(90deg, #6366f1, #f97316);
  display: block;
  margin: 14px auto 0;
  animation: headingLine 0.8s ease forwards;
}

@keyframes headingLine {
  0% { width: 0; opacity: 0; }
  100% { width: 90px; opacity: 1; }
}

/* Subtitle */
.services-sub {
  font-size: 17px;
  color: #4b5563;
  opacity: 0.85;
  max-width: 650px;
  margin: 10px auto 40px;
  line-height: 1.6;
}

/* GRID */
.services-grid {
  max-width: 1200px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 32px;
}

/* CARD STYLE */
.enhanced-card {
  background: #ffffff;
  padding: 32px 26px;
  border-radius: 18px;
  border: 1px solid #e5e7eb;
  box-shadow: 0 8px 20px rgba(0,0,0,0.05);
  transition: 0.3s ease;
  cursor: pointer;
  text-align: center;
}

/* Hover animation */
.enhanced-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 18px 36px rgba(0,0,0,0.12);
}

/* Icon Circle */
.service-icon-circle {
  width: 58px;
  height: 58px;
  border-radius: 50%;
  background: linear-gradient(135deg, #f97316, #fb923c);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 14px;
  color: white;
  font-size: 26px;
  font-weight: bold;
  box-shadow: 0 10px 18px rgba(249,115,22,0.3);
  transition: 0.3s ease;
}

.enhanced-card:hover .service-icon-circle {
  transform: scale(1.1);
}

/* Text */
.enhanced-card h3 {
  font-size: 20px;
  font-weight: 700;
  color: #111827;
  margin-bottom: 8px;
}

.enhanced-card p {
  font-size: 15px;
  color: #4b5563;
  min-height: 40px;
}

/* Button */
.service-explore-btn {
  margin-top: 18px;
  padding: 10px 18px;
  border-radius: 10px;
  background: #4f46e5;
  color: white;
  border: none;
  font-weight: 600;
  transition: 0.25s ease;
}

.service-explore-btn:hover {
  background: #3730a3;
}


/* ============= BUSINESS STAGES ============= */
.business-stage {
  padding: 100px 20px;
  text-align: center;
}

.stage-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 26px;
  margin-top: 38px;
}

.stage-card {
  padding: 28px;
  border-radius: 18px;
  font-weight: 600;
  transition: 0.25s;
}

.stage-card:hover {
  transform: translateY(-6px);
}

.blue { background: #e0f2fe; }
.yellow { background: #fef3c7; }
.red { background: #fee2e2; }
.green { background: #dcfce7; }

/* ============= FINAL CTA ============= */
.final-clean-cta {
  background: linear-gradient(135deg, #1d4ed8, #4f46e5);
  color: white;
  text-align: center;
  padding: 110px 20px;
}

.final-clean-cta h2 {
  margin-bottom: 14px;
}

.final-clean-cta button {
  margin-top: 24px;
  background: #f97316;
  color: white;
  border-radius: 999px;
  padding: 14px 44px;
  border: none;
  font-weight: 600;
  transition: 0.3s;
}

.final-clean-cta button:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 30px rgba(249,115,22,0.45);
}

/* ============= MOBILE FIXES ============= */
@media (max-width: 768px) {
  .metrics-section {
    flex-direction: column;
    gap: 40px;
  }

  .why-clean {
    grid-template-columns: 1fr;
    gap: 50px;
    padding: 70px 20px;
  }

  .hero-buttons {
    flex-direction: column;
  }
}
/* ================================================
   SCROLL REVEAL ANIMATIONS
   ================================================ */

/* Fade Up */
.reveal {
  opacity: 0;
  transform: translateY(40px);
  transition: all 0.9s ease;
}

.reveal.active {
  opacity: 1;
  transform: translateY(0);
}

/* Slide From Left */
.reveal-left {
  opacity: 0;
  transform: translateX(-60px);
  transition: all 0.9s ease;
}

.reveal-left.active {
  opacity: 1;
  transform: translateX(0);
}

/* Slide From Right */
.reveal-right {
  opacity: 0;
  transform: translateX(60px);
  transition: all 0.9s ease;
}

.reveal-right.active {
  opacity: 1;
  transform: translateX(0);
}

/* Scale Up */
.scale-up {
  opacity: 0;
  transform: scale(0.85);
  transition: all 0.9s ease;
}

.scale-up.active {
  opacity: 1;
  transform: scale(1);
}
