.clean-header {
  position: sticky;
  top: 0;
  z-index: 999;
  background: #ffffff;
  border-bottom: 1px solid #e5e7eb;
}

.clean-header-inner {
  max-width: 1280px;
  margin: auto;
  padding: 14px 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* LOGO */
.clean-logo {
  font-size: 20px;
  font-weight: 700;
  cursor: pointer;
  color: #111827;
}

/* NAV (Desktop) */
.clean-nav {
  display: flex;
  gap: 28px;
  font-size: 14px;
  font-weight: 500;
  align-items: center;
}

.clean-nav span {
  cursor: pointer;
  color: #374151;
}

.clean-nav span:hover,
.dropdown-link:hover {
  color: #f97316;
  text-shadow: 0 0 6px rgba(249, 115, 22, 0.6);
}

/* ACTIONS */
.clean-actions {
  display: flex;
  align-items: center;
  gap: 14px;
}

/* BUTTONS */
.clean-login {
  background: transparent;
  border: none;
  font-weight: 500;
  cursor: pointer;
  color: #374151;
}

.clean-primary {
  background: #f97316;
  color: white;
  border: none;
  padding: 6px 16px;
  border-radius: 999px;
  font-weight: 600;
  cursor: pointer;
}

/* PROFILE */
.clean-profile-dropdown {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.clean-avatar {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background: #f97316;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

.profile-name {
  font-size: 14px;
  color: #374151;
}

/* MOBILE MENU ICON */
.mobile-menu-icon {
  display: none;
  font-size: 22px;
  cursor: pointer;
}

/* --------------------------
   MOBILE RESPONSIVE
--------------------------- */
@media (max-width: 768px) {

  .clean-actions {
    display: none;
  }

  .mobile-menu-icon {
    display: block;
  }

  .clean-nav {
    position: fixed;
    top: 0;
    right: -100%;
    width: 70%;
    height: 100vh;
    background: white;
    display: flex;
    flex-direction: column;
    padding: 80px 30px;
    gap: 24px;
    transition: 0.3s ease-in-out;
    box-shadow: -2px 0 6px rgba(0,0,0,0.1);
  }

  .clean-nav.open {
    right: 0;
  }

  .dropdown-link,
  .clean-nav span {
    font-size: 16px;
    padding: 8px 0;
  }

  /* MOBILE USER SECTION */
  .mobile-user-section {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .mobile-user-section button {
    width: 100%;
    background: #ffffff;
    border: 1px solid #d1d5db;
    padding: 10px 14px;
    border-radius: 10px;
    cursor: pointer;
    font-size: 15px;
    font-weight: 500;
    transition: 0.25s ease-in-out;
  }

  /* Neon Hover */
  .mobile-user-section button:hover {
    color: #fff;
    background: #f97316;
    box-shadow: 0 0 10px #f97316, 0 0 20px #f97316;
    border-color: #f97316;
  }

  .logout-btn {
    border-color: #f43f5e !important;
    color: #f43f5e !important;
  }

  .logout-btn:hover {
    background: #f43f5e !important;
    color: #fff !important;
    box-shadow: 0 0 10px #f43f5e, 0 0 20px #f43f5e !important;
  }

  .mobile-avatar {
    width: 44px;
    height: 44px;
    margin: 0 auto;
    font-size: 18px;
  }
}
/* HIDE MOBILE USER SECTION ON DESKTOP */
.mobile-user-section {
  display: none;
}

@media (max-width: 768px) {
  .mobile-user-section {
    display: flex;
  }
}

